body {
  margin: 0;
  padding: 0;
}

.snowflake {
  color: #fff;
  font-size: 1em;
  position: fixed;
  top: -2em;
}
img {
  width: 100%;
}

.wrapper {
  width: 50%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
.card{
  display: flex;
}
span{
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  font-weight: bold;
}
.ant-card-body {
    padding: 10px 15px 15px 15px !important;
}
.conteiner {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
@media (max-width: 600px) {
  .wrapper {
    width: 100%;
    padding: 10px;
  }
  .secret-input {
    width: 70%;
  }
  .text-footer{
    margin-top: 5px;
    margin-left: 25px;
  }
}

.text-timer{
  display: flex;
  justify-content: center;
  color: red;
}